.tarif-container {
    margin: 0 auto;
    padding: 20px;
}

.bacground-container {
    background: linear-gradient(24deg, #414380, transparent 21%), linear-gradient(198deg, #ffdfac 11%, transparent 18%), linear-gradient(50deg, #923880 5%, #ff083f 32%, #fb5e4f 35%, #f85448 65%, #fba64b 83%, #ffa745 91%);
    ;
    height: 100%;
    width: 100%;
    background-size: cover;
    background-repeat: no-repeat;
    border-radius: 30px;
    max-width: 1900px;
    margin: 0 auto;
    display: flex;
    flex-direction: row;
    height: 100%;
    padding: 90px;

}


.left-top h3 {
    max-width: 440px;
    margin-top: 0;
    margin-bottom: 12px;
    font-size: 32px;
    line-height: 1.25;
    font-weight: 600;
    letter-spacing: -.04em;
}

.left-top p {
    z-index: 5;
    max-width: 560px;
    margin-top: 8px;
    margin-bottom: 8px;
    font-size: 19px;
    line-height: 1.4;
    font-weight: 400;
    letter-spacing: -.02em;
    padding: 0;
}

.left-down-top {

    display: flex;
    align-items: center;
    margin-top: 40px;
    margin-bottom: 24px;

}



.left-down-top h4 {

    font-weight: 600;
    letter-spacing: -.01em;
    padding-right: 10px;

}

.div {
    height: 1px;
    width: 100%;
    background: rgb(240, 239, 239);
    flex: 1;
}


.container-left {
    background: white;
    padding: 30px;
    border: none;
    flex: 2;
    border-top-left-radius: 30px;
    border-bottom-left-radius: 30px;


}




.content {
    flex: 1;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 1rem;
}


.container-right {
    background: #f8d9d7;
    padding: 30px;
    position: relative;
    border-top-right-radius: 30px;
    border-bottom-right-radius: 30px;
    flex: 1;

}

.content-right {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    text-align: center;



}

.content-right h3 {
    width: 250px;
    height: 100%;
    margin-bottom: 20px;
    flex-wrap: wrap;

}




.container-right button {
    background: #f9476d;
    color: white;
    padding: 15px 20px 15px 20px;
    border-radius: 30px;
    border: none;
    cursor: pointer;
    font-size: 20px;

}


.container-right button:hover {
    background: rgb(48, 45, 45)
}




@media screen and (max-width: 979px) {

    .tarif-container {
        padding: 10px;

    }


    .bacground-container {
        flex-direction: column;
        padding: 25px;

    }

    .container-right {

        height: 100%;

    }

    .content-right {
        position: absolute;
        text-align: center;
    }

    .container-right {
        height: 100%;
        padding-top: 200px;
        border-top-right-radius: 0px;
        border-bottom-left-radius: 30px;
    }

    .container-left {

        border-top-right-radius: 30px;
        border-bottom-left-radius: 0px;


    }
}

@media screen and (max-width:768px) {
    /* .bacground-container {

        margin-left: 5px;

        display: flex;
        justify-content: center;
    } */

}

@media screen and (max-width: 746px) {

    .left-top h3 {

        font-size: 30px;

    }

    .left-top p {
        font-size: 17px;
    }

    .included-img p {
        font-size: 13px;

    }

    .left-down-top h4 {

        font-size: 15px;

    }

    .content {

        grid-template-columns: repeat(1, 1fr);
        grid-gap: 1rem;
    }


}

@media screen and (max-width: 490px) {


    .left-top h3 {
        font-size: 20px;

    }
}