.container-footer {
    margin: 0 auto;
    padding: 20px;
}


.big-footer {
    background: var(--color-text);
    max-width: 1900px;
    border-radius: 30px;
    display: flex;
    flex-direction: column;
    margin: 0 auto;
}

.big-footer p {
    color: #8a8a8e;

}

.footer-content {


    display: flex;
    flex-direction: row;
    justify-content: space-between;

}

.footer-start {
    display: flex;
    flex-direction: column;

}

.footer-start img {
    width: 100px;


    margin-bottom: 30px;
}

.footer-start p {
    max-width: 400px;
    padding: 0;
}


.footer-center {
    display: flex;
    flex-direction: column;
}

.footer-center a {
    color: white;
    font-size: 32px
}

.footer-center a:hover,
.footer-end a:hover {
    color: #007aff
}

.footer-end {
    display: flex;
    flex-direction: column;
}

.footer-end a {
    color: white;
    font-size: 15px;
    padding-bottom: 10px;
}

.footer-bottom {
    margin-top: 70px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.footer-bottom-end {
    display: flex;

}

.footer-bottom-end img {
    width: 35px;
    background: white;
    padding: 5px;
    border-radius: 20px;
    cursor: pointer;
}

.footer-bottom-end img:nth-child(1) {
    margin-right: 10px;

}

.footer-start p {
    font-size: 19px;
}

.footer-bottom-start p {
    padding: 0px;
}

@media screen and (max-width: 740px) {

    .footer-center a {

        font-size: 25px
    }

    .footer-end a {

        font-size: 12px;

    }

}

@media screen and (max-width: 600px) {
    .container-footer {
        padding: 0px;

    }

    .big-footer {
        border-radius: 0px;
    }

    .footer-content {


        flex-direction: column;

    }

    .footer-end,
    .footer-center {
        margin-top: 25px;
    }

}

@media screen and (max-width: 414px) {

    .footer-bottom {
        flex-direction: column;
    }

    .footer-bottom p {
        padding: 0;
        margin-bottom: 20px;
    }

    .footer-end a {
        font-size: 16px;
    }

}

@media screen and (max-width: 375px) {

    .big-footer {}



}